<template>
  <div :class="className" :style="{ height: height, width: width }" />
</template>

<script>
import * as echarts from "echarts";
require("echarts/theme/macarons"); // echarts theme
import resize from "./mixins/resize";

let now = new Date(1997, 9, 3);
// let oneDay = 24 * 3600 * 1000;
let value = Math.random() * 1000;

export default {
  mixins: [resize],
  props: {
    className: {
      type: String,
      default: "chart",
    },
    width: {
      type: String,
      default: "100%",
    },
    height: {
      type: String,
      default: "600px",
    },
    autoResize: {
      type: Boolean,
      default: true,
    },
    testData11: {
      type: Array,
      default: [
        ["2022-11-09 17:54:43", 11],
        ["2022-11-09 17:56:43", 13],
      ],
    },
    // oxygenData: String,
    // temprature: String
    // chartData: {
    //   type: Object,
    //   required: true,
    // },
  },
  data() {
    return {
      chart: null,
      testData: [],
    };
  },
  // watch: {
  //   chartData: {
  //     deep: true,
  //     handler(val) {
  //       this.setOptions(val);
  //     },
  //   },
  // },
  watch: {
    testData11: {
      handler(val) {
        this.chart.setOption({
          series: [
            {
              data: val,
            },
          ],
        });
      },
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart();
    });
    console.log("testdata:", this.testData11);
    // for (var i = 0; i < 6; i++) {
    //   this.testData.push(this.randomData());
    // }
    // this.createData();
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
    clearInterval(this.dataTimer);
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el, "macarons");
      this.setOptions();
    },
    setOptions() {
      this.chart.setOption({
        xAxis: {
          type: "time",
          // min:"2022-11-09 17:54:43",
          // max:"2022-11-09 17:56:43",
          //data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
          // data:["2022-11-09 17:54:43","2022-11-09 17:54:53","2022-11-09 17:55:43"],
          boundaryGap: false,
          axisLabel: {
            align: "center",
            formatter: function (value, index) {
              // 格式化成时分秒
              // console.log('index:', index)
              console.log("xaxis:", value);
              let date = new Date(value);
              console.log(date);
              let nowDay = date.getMinutes().toString().padStart(2, "0");
              let nowHour = date.getHours().toString().padStart(2, "0");
              let nowMin = date.getMinutes().toString().padStart(2, "0");
              let nowSeconds = date.getSeconds().toString().padStart(2, "0");
              var texts = [nowHour, nowMin];
              console.log(texts);
              return texts.join(":");
              // return nowSeconds.toString();
            },
          },
          splitLine: {
            show: false,
          },
        },
        grid: {
          left: 10,
          right: 10,
          bottom: 20,
          top: 30,
          containLabel: true,
        },
        dataZoom: {
          type: "inside",
        },
        tooltip: {
          trigger: "axis",
          backgroundColor: "#00A1F1",
          formatter: function (params) {
            console.log(params);
            params = params[0];
            // var date = new Date(params.name);
            var date = new Date(params.data[0]);
            return (
              date.getFullYear() +
              "-" +
              (date.getMonth() + 1) +
              "-" +
              date.getDate() +
              " " +
              date.getHours() +
              ":" +
              date.getMinutes().toString().padStart(2, "0") +
              ":" +
              date.getSeconds().toString().padStart(2, "0") +
              " -> " +
              params.value[1]
            );
          },
          axisPointer: {
            // type: "cross",
            animation: false,
          },
          padding: [5, 10],
        },
        yAxis: {
          type: "value",
          axisTick: {
            show: false,
          },
        },
        legend: {
          // data: ["expected", "actual"],
          data: ["expected"],
        },
        series: [
          {
            name: "expected",
            markLine: {
              //最大值和最小值
              data: [
                {
                  name: "阈值",
                  yAxis: 3,//阈值设置
                  label: {
                    show: "true",
                    position: "middle",
                    formatter: "溶氧红线3",
                    fontSize: 20
                  },
                  lineStyle: {
                    normal: {
                      width: 2,
                      color: "#FF5D1D",
                    },
                  },
                },
              ],
            },
            itemStyle: {
              normal: {
                color: "#FBBC05",
                lineStyle: {
                  // color: "#FF005A",
                  color: "#FBBC05",
                  width: 2,
                },
              },
            },
            smooth: true,
            type: "line",
            // data: expectedData,
            data: this.testData11,
            // data: [['2022-11-09 17:54:43',11],['2022-11-09 17:55:43',12],['2022-11-09 17:56:43',15]],
            // animationDuration: 2800,
            animationEasing: "cubicInOut",
          },
        ],
      });
    },
    createData() {
      let that = this;
      that.dataTimer = setInterval(() => {
        that.testData.shift();
        that.testData.push(that.randomData());
        // console.log('testdata:', that.testData)
        that.chart.setOption({
          series: [
            {
              data: that.testData,
            },
          ],
        });
      }, 1000);
    },

    randomData() {
      // now = new Date(+now + oneDay);
      now = new Date();
      value = value + Math.random() * 21 - 10;
      // console.log('value:', value)
      // this.oxygenData = value;
      this.$emit("temperChange", Math.round(value));
      console.log("name:", now.toString());
      return {
        name: now.toString(),
        value: [
          [now.getFullYear(), now.getMonth(), now.getDate()].join("/") +
            " " +
            now.getHours() +
            ":" +
            now.getMinutes().toString().padStart(2, "0") +
            ":" +
            now.getSeconds().toString().padStart(2, "0"),
          // [now.getHours(), now.getMinutes(), now.getSeconds()].join(":"),
          // now.toString(),
          Math.round(value),
        ],
      };
    },
  },
};
</script>
