<template>
  <div class="login-container">
    <van-nav-bar title="历史数据" left-text="返回" left-arrow>
      <template #right>
        <van-icon name="search" size="18" />
      </template>
    </van-nav-bar>
    <van-tabs type="line" @click="onClickHoursData">
      <van-tab title="1小时"></van-tab>
      <van-tab title="6小时"></van-tab>
      <van-tab title="12小时"></van-tab>
      <van-tab title="自定义">
        <van-cell
          title="选择日期区间"
          :value="canlendarData"
          @click="isShowCalendar = true"
        />
        <van-calendar
          v-model="isShowCalendar"
          type="range"
          allow-same-day
          :min-date="startMin"
          :max-date="new Date()"
          @confirm="onConfirm"
        />
      </van-tab>
    </van-tabs>
    <LineChart v-on:temperChange="updateTemp" :testData11="testresponse" />
  </div>
</template>

<script>
import LineChart from "./components/LineChart";
import { getDataHours,getDataDays } from "@/api/index";

export default {
  name: "HomePage",
  components: {
    LineChart,
  },
  data() {
    return {
      ischecked: false,
      active: 0,
      oxygenData: 0,
      temprature: 0,
      isShowCalendar: false,
      canlendarData: "",
      testresponse: [],
      startMin: this.setStartMin(),
    };
  },
  watch: {
    $route: {
      handler: function (route) {
        this.redirect = route.query && route.query.redirect;
      },
      immediate: true,
    },
  },
  methods: {
    showPwd() {
      if (this.passwordType === "password") {
        this.passwordType = "";
      } else {
        this.passwordType = "password";
      }
      this.$nextTick(() => {
        this.$refs.password.focus();
      });
    },
    setStartMin() {
      var now = new Date();
      return new Date(now.getFullYear(), now.getMonth() - 6, now.getDate());
    },
    endMax() {
      var now = new Date();
      return new Date(now.getFullYear(), now.getMonth() + 6, now.getDate());
    },
    updateTemp(e) {
      this.temprature = e;
    },
    updateOxygen(e) {
      this.oxygenData = e;
    },
    formatDate(date) {
      return `${date.getMonth() + 1}/${date.getDate()}`;
    },
    onConfirm(date) {
      const [start, end] = date;
      let aend = new Date(end.getFullYear(), end.getMonth(), end.getDate()+1)
      this.isShowCalendar = false;
      this.canlendarData = `${this.formatDate(start)} - ${this.formatDate(
        end
      )}`;
      console.log('start:',typeof(start.valueOf()))
      console.log(start.valueOf())
      console.log('end:',typeof(aend))
      console.log(start.valueOf()/1000)
      new Promise((resolve, reject) => {
        getDataDays(start.valueOf()/1000, aend.valueOf()/1000).then((response) => {
          const { data } = response;
          this.testresponse = data;
          console.log("data:", response);
        });
      });
    },
    onClickHoursData(name, title) {
      let hours = 0;
      switch (title) {
        case "1小时":
          hours = 1;
          break;
        case "6小时":
          hours = 6;
          break;
        case "12小时":
          hours = 12;
          break;
        default:
          hours = 1;
          break;
      }
      new Promise((resolve, reject) => {
        getDataHours(hours).then((response) => {
          const { data } = response;
          this.testresponse = data;
          console.log("data:", data);
        });
      });
    },
  },
};
</script>

<style lang="scss">
/* 修复input 背景不协调 和光标变色 */
/* Detail see https://github.com/PanJiaChen/vue-element-admin/pull/927 */

$bg: #283443;
$light_gray: #fff;
$cursor: #fff;

@supports (-webkit-mask: none) and (not (cater-color: $cursor)) {
  .login-container .el-input input {
    color: $cursor;
  }
}

/* reset element-ui css */
.login-container {
  .el-input {
    display: inline-block;
    height: 47px;
    width: 85%;

    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: $light_gray;
      height: 47px;
      caret-color: $cursor;

      &:-webkit-autofill {
        box-shadow: 0 0 0px 1000px $bg inset !important;
        -webkit-text-fill-color: $cursor !important;
      }
    }
  }

  .el-form-item {
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    color: #454545;
  }
}
</style>

<style lang="scss" scoped>
$bg: #2d3a4b;
$dark_gray: #889aa4;
$light_gray: #eee;

.login-container {
  min-height: 100%;
  width: 100%;
  background-color: $bg;
  overflow: hidden;

  .login-form {
    position: relative;
    width: 520px;
    max-width: 100%;
    padding: 160px 35px 0;
    margin: 0 auto;
    overflow: hidden;
  }

  .tips {
    font-size: 14px;
    color: #fff;
    margin-bottom: 10px;

    span {
      &:first-of-type {
        margin-right: 16px;
      }
    }
  }

  .svg-container {
    padding: 6px 5px 6px 15px;
    color: $dark_gray;
    vertical-align: middle;
    width: 30px;
    display: inline-block;
  }

  .title-container {
    position: relative;

    .title {
      font-size: 26px;
      color: $light_gray;
      margin: 0px auto 40px auto;
      text-align: center;
      font-weight: bold;
    }
  }

  .show-pwd {
    position: absolute;
    right: 10px;
    top: 7px;
    font-size: 16px;
    color: $dark_gray;
    cursor: pointer;
    user-select: none;
  }
}
</style>
